html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.29;
  letter-spacing: normal;
  text-align: left;
  color: #444444;

  @media (min-width: $tablet-width) {
    font-size: 16px;
    line-height: 1.25;
  }
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  font-size: 0;
}

.burger {
  display: inline-block;
  vertical-align: middle;
  width: 25px;
  height: 19px;
  background-image: url("../img/icon-mobile-menu.svg");
  background-repeat: no-repeat;
}

.header-back::before {
  @media (min-width: $desktop-width) {
    position: absolute;
    content: "";
    background-image: url("../img/index-background-desktop@1x.jpg"), linear-gradient(to right, #ffffff 50%, #63a73a 50%);
    background-repeat: no-repeat, no-repeat;
    background-position: 100% 0, 0 0;
    background-size: 650px 696px, auto;
    width: 100%;
    height: 696px;
    z-index: -100;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../../img/index-background-desktop@2x.jpg"), linear-gradient(to right, #ffffff 50%, #63a73a 50%);
      background-size: 650px 696px, auto;
    }
  }
}
