.contacts {
  padding: 31px 0 26px 0;
  background-color: $secondary-color;

  @media (min-width: $tablet-width) {
    padding: 70px 0 62px 0;
  }

  &--index {
    background-color: #ffffff;
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    top: 95px;
    left: 40px;
    width: 570px;
    height: 201px;
    background-color: #ffffff;
    z-index: 10;
  }

  @media (min-width: 1301px) {
    left: 15vw;
  }
}

.contacts__wrapper {
  @include container();
  display: flex;

  @media (min-width: $tablet-width) {
    padding: 0 70px;
    justify-content: space-between;
  }
}

.contacts__heading {
  margin: 0 18px 0 0;
  font-family: $font-stack;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.25;
  color: $heading-text-color;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    width: 155px;
    font-size: 20px;
  }
}

.contacts__description {
  margin: 0;
  line-height: 1.43;

  @media (min-width: $tablet-width) {
    text-align: right;
    width: 155px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  @media (min-width: $desktop-width) {
    text-align: left;
  }
}
